<template lang="pug">
include ../../mixins/bemto/bemto
+b.user-settings-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.go(-1)")
  +e.form.FORM
    fieldset
      +b.heading-text.--h2.--right.LEGEND Change User Data
      Input(
        v-for="(input, idx) in inputs",
        :key="idx",
        :propsToMerge="input",
        @input-handler="inputHandler"
      )
      +e.buttons
        ButtonType1(
          :title="'change data'",
          :disabled="$v.form.$invalid||!$v.$anyDirty",
          @click-handler="updateProfileData(form)"
        )
</template>

<script>
import { required, email, minLength } from 'vuelidate/lib/validators'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import inputsFields from '@/views/UserSettings/inputsFields.js'
import validateErrorHandler from '@/mixins/validateErrorHandler'

export default {
  mixins: [inputsFields, validateErrorHandler],
  data: () => ({
    form: {
      name: '',
      email: '',
    },
  }),
  validations: {
    form: {
      name: {
        required,
        minLength: minLength(1),
      },
      email: {
        required,
        email,
      },
    },
  },
  mounted() {
    this.form.name = this.userData.name
    this.form.email = this.userData.email
  },
  computed: {
    ...mapGetters(['userData']),
  },
  methods: {
    ...mapActions(['createUser', 'updateProfileData']),
    ...mapMutations(['SET_USER_NAME', 'SET_INFO_MESSAGE']),
    inputHandler(val, model) {
      this.form[model] = val.trim()
      this.$v.form[model].$touch()
    },
    registerUser() {
      if (this.$v.form.$invalid) {
        this.$v.form.$touch()
        return
      }
      this.createUser({ ...this.form })
    },
  },
}
</script>
