<template lang="pug">
include ../../../mixins/bemto/bemto
+b.song-text
  +e.content
    +e.controls
      +e.controls-element
        InputSelect(:options="fontSizeOptions" @change-handler="changeFontHandler" :selected="fzMod")
      +e.controls-element
        InputSelect(:options="scrollOptions" @change-handler="speedHandler" :selected="scrollSpeed")
      +e.controls-element
        ButtonType1(title='up' @click-handler="upBtnHandler")
      +e.controls-element
        ButtonType1(title='sync' @click-handler="TOGGLE_SYNCED" :color="synced?'accent-light':''")
      +e.controls-element
        ButtonType1(:title="isScrolling? '' : ''" @click-handler="scrollHandler" :color="isScrolling?'accent-light':''")
    +e.text(v-if="song.text", :class="`song-text__text--${fzMod}`")
      textarea(:value='song.text', :disabled='true' ref="scrollContainer")
    +e.no-text(v-else)
      +b.text.--p3.--center text is not available
      +b.link.--l3.--accent.--center(@click.stop="$router.push(`editSong/${song.id}`)") + add song text
</template>
<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data: () => ({
    fontSizeOptions: [
      { title: 'small', value: 'small', selected: false },
      { title: 'medium', value: 'medium', selected: false },
      { title: 'large', value: 'large', selected: false },
    ],
    scrollOptions: [
      { title: 'speed 1', value: 500000, selected: false },
      { title: 'speed 2', value: 400000, selected: false },
      { title: 'speed 3', value: 300000, selected: false },
      { title: 'speed 4', value: 200000, selected: false },
      { title: 'speed 5', value: 100000, selected: false },
      { title: 'speed 6', value: 50000, selected: false },
      { title: 'speed 7', value: 30000, selected: false },
    ],
  }),
  computed: {
    ...mapGetters(['song', 'fzMod', 'scrollSpeed', 'synced', 'isScrolling']),
    textAreaConfig() {
      return {
        Input: {
          componentName: 'Input',
          fieldName: 'text',
          childComponentName: 'NeuroInputText',
        },
        NeuroInputText: {
          modelIdent: 'text',
          componentName: 'NeuroInputText',
          value: this.song.text,
          inputType: 'textarea',
          disabled: true,
        },
      }
    },
  },
  mounted() {
    this.$root.$refs.scrollContainer = this.$refs.scrollContainer
  },
  methods: {
    ...mapMutations([
      'LOAD_SONG',
      'DELETE_SONG',
      'CHANGE_FZ_MOD',
      'CHANGE_SCROLL_SPEED',
      'TOGGLE_SYNCED',
    ]),
    changeFontHandler(mod) {
      this.CHANGE_FZ_MOD(mod)
    },
    speedHandler(speed) {
      this.CHANGE_SCROLL_SPEED(speed)
    },
    upBtnHandler() {
      this.$scrollTo(this.$refs.scrollContainer, 500, { offset: 0 })
    },
    scrollHandler() {
      this.$root.$emit('scroll-play')
    },
  },
}
</script>
