<template lang="pug">
include ../../mixins/bemto/bemto
+b.reset-password-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.go(-1)")
  +e.form.FORM
    fieldset
      +b.heading-text.--h2.--right.LEGEND Reset Password
      Input(
        v-for="(input, idx) in inputs",
        :key="idx",
        :propsToMerge="input",
        @input-handler="inputHandler"
      )
      +e.buttons
        ButtonType1(
          :title="'reset password'",
          :disabled="$v.form.$invalid",
          @click-handler="resetPassword(form.email)"
        )
</template>

<script>
/* eslint-disable */
import { required, email } from 'vuelidate/lib/validators'
import { mapActions } from 'vuex'
import inputsFields from '@/views/ResetPassword/inputsFields.js'
import validateErrorHandler from '@/mixins/validateErrorHandler'

export default {
  mixins: [inputsFields, validateErrorHandler],
  data: () => ({
    show: false,
    uploadedFile: {},
    form: {
      email: '',
    },
  }),
  validations: {
    form: {
      email: {
        required,
        email,
      },
    },
  },
  methods: {
    ...mapActions(['resetPassword']),
    inputHandler(val, model) {
      this.form[model] = val.trim()
      this.$v.form[model].$touch()
    },
  },
}
</script>
