const defaultState = () => {
  return {
    themeDark: true,
    volume: -5,
    lastVolumeValue: -5,
    mute: false,
    selectedSound: 'click',
    avatar: null,
    fzMod: 'medium',
    scrollSpeed: '4000',
    synced: true,
    tuningNote: 'A2',
  }
}
const settings = {
  state: defaultState(),
  getters: {
    settings: state => state,
    themeDark: state => state.themeDark,
    volume: state => state.volume,
    selectedSound: state => state.selectedSound,
    avatar: state => state.avatar,
    fzMod: state => state.fzMod,
    scrollSpeed: state => state.scrollSpeed,
    synced: state => state.synced,
    tuningNote: state => state.tuningNote,
  },
  mutations: {
    SET_SETTINGS(state, data) {
      Object.assign(state, data)
      this.commit('UPDATE_THEME')
    },
    RESET_SETTINGS_STATE(state) {
      Object.assign(state, defaultState())
    },
    CHANGE_THEME(state) {
      state.themeDark = !state.themeDark
      this.commit('UPDATE_THEME')
    },
    UPDATE_THEME(state) {
      const html = document.documentElement
      if (state.themeDark) html.removeAttribute('data-theme')
      else html.setAttribute('data-theme', 'light')
    },
    SET_AVATAR(state, avatar) {
      state.avatar = avatar
    },
    CHANGE_FZ_MOD(state, value) {
      state.fzMod = value
    },
    CHANGE_SCROLL_SPEED(state, value) {
      state.scrollSpeed = value
    },
    CHANGE_VOL(state, value) {
      state.volume = value
    },
    CHANGE_SELECTED_SOUND(state, value) {
      state.selectedSound = value
    },
    TOGGLE_SYNCED(state) {
      state.synced = !state.synced
    },
    CHANGE_NOTE(state, note) {
      state.tuningNote = note
    },
  },
}
export default settings
