import { version } from '../../package.json'
import { name } from '../../package.json'
const defaultState = () => {
  return {
    isLoading: false,
    isSongUpdated: false,
    isScrolling: false,
    isPlaying: false,
    menuIsOpen: false,
    beatsMenuIsOpen: false,
    snackbar: {
      show: false,
      delay: 1000,
      text: '',
      type: 'info',
      timeout: 3000,
    },
  }
}
const system = {
  state: defaultState(),
  getters: {
    version: () => version,
    isLoading: state => state.isLoading,
    isUpdated: state => state.isSongUpdated,
    menuIsOpen: state => state.menuIsOpen,
    beatsMenuIsOpen: state => state.beatsMenuIsOpen,
    snackbar: state => state.snackbar,
    appName: () => name,
    isScrolling: state => state.isScrolling,
    isPlaying: state => state.isPlaying,
  },
  mutations: {
    RESET_SYSTEM_STATE(state) {
      Object.assign(state, defaultState())
    },
    TURN_ON_LOADER(state) {
      state.isLoading = true
    },
    TURN_OFF_LOADER(state) {
      state.isLoading = false
    },
    TOGGLE_MENU(state, { valueName, delay = 0 }) {
      setTimeout(() => {
        const currentValue = state[valueName]
        state[valueName] = !currentValue
      }, delay)
    },
    CLOSE_ALL_MENU(state) {
      const menus = ['menuIsOpen', 'beatsMenuIsOpen']
      menus.forEach(menu => {
        state[menu] = false
      })
    },
    SET_SONG_UPDATED(state) {
      state.isSongUpdated = true
    },
    RESET_SONG_UPDATED(state) {
      state.isSongUpdated = false
    },
    SHOW_SNACKBAR(state, message) {
      if (!message.text) return
      Object.assign(state.snackbar, message)
      if (message.type === 'error' && !message.timeout)
        state.snackbar.timeout = 10000
      setTimeout(() => {
        state.snackbar.show = true
        setTimeout(() => {
          this.commit('HIDE_SNACKBAR')
        }, state.snackbar.timeout)
      }, state.snackbar.delay)
    },
    HIDE_SNACKBAR(state) {
      state.snackbar.show = false
      setTimeout(() => {
        Object.assign(state.snackbar, defaultState().snackbar)
      }, 500)
    },
    SCROLL_START(state) {
      state.isScrolling = true
    },
    SCROLL_STOP(state) {
      state.isScrolling = false
    },
    CLICK_START(state) {
      state.isPlaying = true
    },
    CLICK_STOP(state) {
      state.isPlaying = false
    },
  },
}

export default system
