<template lang="pug">
include ../../mixins/bemto/bemto
+b.add-song-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.go(-1)")
  +b.info
    +b.heading-text.--h2.--right {{title}}
    +b.text.--p3.--right {{ song.author }} - {{ song.title }} {{ song.bpm }} bpm {{ song.beats }}/{{ song.size }}
  +e.content
    Input(
      v-for="(input, idx) in inputs",
      :key="idx",
      :propsToMerge="input",
      @input-handler="inputHandler"
    )
  +e.save-btn
    ButtonType1(:title="btnLabel" @click-handler="clickHandler" :disabled="$v.song.$anyError" @keyup.enter="clickHandler")

</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'
import { mapGetters, mapMutations } from 'vuex'
import validateErrorHandler from '@/mixins/validateErrorHandler'
import inputsFields from '@/views/AddSong/inputsFields.js'
import uniqid from 'uniqid'


export default {
  data: () => ({
    song: {
      id: null,
      author: 'Author',
      title: 'SongName',
      text: '',
      bpm: 120,
      beats: 4,
      size: 4,
      sFirstBeat: true,
    },
  }),
  validations: {
    song: {
      author: {
        required,
        minLength: minLength(1),
      },
      title: {
        required,
        minLength: minLength(1),
      },
    },
  },
  mixins: [inputsFields, validateErrorHandler],
  created() {
    const id = this.$route.params.id
    if (id) this.song = this.songById(id)
  },
  methods: {
    ...mapMutations(['ADD_SONG', 'UPDATE_SONG']),
    inputHandler(val, model) {
      // fix switch value
      const newVal = typeof val === 'boolean' ? !val : val
      this.song[model] = newVal
      this.$v.song[model]?.$touch()
    },
    clickHandler() {
      if (this.$v.song.$invalid) {
        this.$v.song.$touch()
        return
      }
      if (this.song.id) {
        this.UPDATE_SONG(this.song)
      } else {
        this.song.id = uniqid()
        this.ADD_SONG(this.song)
      }
    },
  },
  computed: {
    ...mapGetters(['constants', 'songById', 'colors']),
    title() {
      return this.song.id ? 'Edit Song' : 'Add Song'
    },
    btnLabel() {
      return this.song.id ? 'save' : 'add Song'
    },
  },
}
</script>

