<template lang="pug">
include ../../../mixins/bemto/bemto
+b.menu(:class="{ 'menu--active': menuIsOpen }")
  +e.content
    +e.auth(v-if="isAuth")
      +e.element
        +e.avatar
          input(type='file' @change="avatarHandler($event)")
          img(v-if="avatarSrc" :src='avatarSrc')
          .icon.icon-user(v-else)
      +e.element(@click="userSettingsClickHandler")
        +b.text.--p3.--accent {{ email }}
        +b.heading-text.--h2.--accent.--center {{ name }}
      +e.element.text--p2(@click="menuClickHandler('AddSong')") add song
      +e.element.text.--p2(@click="menuClickHandler('Songs')") songs
    +e.common
      +e.element.text--p2(@click="menuClickHandler('About')") about
      +e.element.text--p2(v-if="isAuth", @click="logOut")
        +e.log-out logout
      +e.element.text.--p2(v-else, @click="menuClickHandler('signIn')") sign-in
      +e.element.text.--p2
        +e.tuning-fork
          InputSelect(:options="noteOptions" @change-handler="changeNote" :selected="tuningNote")
          img(src="../../../../public/img/turning-fork.svg" @click="playFork")
      +e.element
        +e.theme-switcher
          NeuroInputSwitch(
            :defininglass="'theme'",
            @input-handler="CHANGE_THEME",
            :isActive="themeDark"
          )
    +e.app-version
      +b.text.--p4.--center {{ appName }}
      +b.text.--p4.--center ver.{{ version }}
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'

export default {
  components: {},
  data: () => ({
    noteOptions: [
      { title: 'E4', value: 'E4', selected: false },
      { title: 'B3', value: 'B3', selected: false },
      { title: 'G3', value: 'G3', selected: false },
      { title: 'D3', value: 'D3', selected: false },
      { title: 'A2', value: 'A2', selected: false },
      { title: 'E2', value: 'E2', selected: false },
    ],
  }),
  methods: {
    ...mapActions(['logOut']),
    ...mapMutations([
      'CHANGE_THEME',
      'CLOSE_ALL_MENU',
      'SET_AVATAR',
      'CHANGE_NOTE',
    ]),
    playFork() {
      this.$root.$emit('tuning-fork')
    },
    menuClickHandler(route) {
      this.CLOSE_ALL_MENU()
      this.$router.push(route)
    },
    userSettingsClickHandler() {
      this.CLOSE_ALL_MENU()
      this.$router.push('/userSettings')
    },
    avatarHandler(event) {
      const file = event.target.files[0]
      this.imageToBase64(file)
    },
    imageToBase64(file) {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => {
        this.SET_AVATAR(reader.result)
      }
      reader.onerror = error => {
        this.$clg(`Cant convert image: ${error}`, 'success')
      }
    },
    changeNote(note) {
      this.CHANGE_NOTE(note)
    },
  },
  computed: {
    ...mapGetters([
      'menuIsOpen',
      'name',
      'alternativeAvatar',
      'isAuth',
      'version',
      'themeDark',
      'avatar',
      'email',
      'appName',
      'tuningNote',
    ]),
    avatarSrc() {
      return this.avatar || this.alternativeAvatar || ''
    },
  },
}
</script>
