export default {
  computed: {
    inputs() {
      return [
        {
          Input: {
            componentName: 'Input',
            label: 'BPM',
            fieldName: 'beatsMenu-bpm',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'bpm',
            componentName: 'NeuroInputSlider',
            min: this.constants.minBpm,
            max: this.constants.maxBpm,
            value: this.song.bpm,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'BEATS',
            fieldName: 'beatsMenu-beats',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'beats',
            componentName: 'NeuroInputSlider',
            value: this.song.beats,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'SIZE',
            fieldName: 'beatsMenu-size',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'size',
            componentName: 'NeuroInputSlider',
            min: 2,
            max: 16,
            step: 2,
            value: this.song.size,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'STRESS FIRST BEAT',
            fieldName: 'beatsMenu-stressFirstBeat',
            childComponentName: 'NeuroInputSwitch',
          },
          NeuroInputSwitch: {
            modelIdent: 'sFirstBeat',
            componentName: 'NeuroInputSwitch',
            isActive: this.song.sFirstBeat,
          },
        },
      ]
    },
  },
}
