const defaultState = () => {
  return {
    allSounds: [
      { path: 'click', name: 'click' },
      { path: 'rim-shot', name: 'rim-shot' },
      { path: 'hi-hat', name: 'hi-hat' },
    ],
    allTunerSounds: ['E2', 'B3', 'G3', 'D3', 'A2', 'E4'],
    minVolumeDb: -35,
    maxVolumeDb: 0,
    minBpm: 20,
    maxBpm: 240,
    // If color variables will change do not forget to change it in theme.scss
    colors: {
      accentLight: '#109ef1',
      secondAccentLight: 'rgb(253, 255, 122)',
      textGrey: '#74797d',
      error: 'rgba(231, 91, 91, 0.932);',
      warn: '#ff8000',
      success: 'rgba(93, 230, 93, 0.863)',
      mainBg: '#212529',
    },
    themeLightColors: {
      mainBg: '#ecf0f3',
    },
  }
}
const constants = {
  state: defaultState(),
  getters: {
    constants: state => state,
    allSounds: state => state.allSounds,
    allTunerSounds: state => state.allTunerSounds,
    minVolumeDb: state => state.minVolumeDb,
    maxVolumeDb: state => state.maxVolumeDb,
    minBpm: state => state.minBpm,
    maxBpm: state => state.maxBpm,
    themeVariables: (state, getters) => {
      return getters.themeDark
        ? state.colors
        : { ...state.colors, ...state.themeLightColors }
    },
    colors: state => state.colors,
  },
}
export default constants
