import * as Tone from 'tone'
import { mapGetters, mapMutations } from 'vuex'
const metronomeHandler = {
  data: () => ({
    loop: {},
    beatCounter: 0,
    toneVolumeNode: null,
    sounds: {},
    sndHigh: null,
    sndLow: null,
    beatsDOM: [],
    currentVolume: 0,
    scrollCancel: null,
    tunerSounds: {},
    tuningFork: null,
  }),

  created() {
    this.loadSound()
    this.loadTunerSound()
    this.setSound()
    this.currentVolume = this.volume
    Tone.Transport.bpm.value = this.song.bpm
  },

  mounted() {
    this.$root.$on('current-volume', (value, isMute) => {
      this.currentVolume = value
      Tone.Master.mute = isMute
    })

    this.$root.$on('scroll-play', () => {
      this.playStop('scroll')
    })

    this.$root.$on('tuning-fork', () => {
      this.tuningFork = this.tunerSounds[this.tuningNote]
      this.tuningFork.start()
    })

    this.updateBeats()
    this.$store.subscribe(mutation => {
      switch (mutation.type) {
        case 'CHANGE_CURRENT_BPM':
        case 'INCREASE_CURRENT_BPM':
        case 'DECREASE_CURRENT_BPM':
          Tone.Transport.bpm.value = this.song.bpm
          break
        case 'CHANGE_CURRENT_SIZE':
          this.loop.interval = this.song.size + 'n'
          break
        case 'CHANGE_SELECTED_SOUND':
          this.setSound()
          break
        case 'CHANGE_VOL':
          this.toneVolumeNode.volume.value = this.currentVolume
          break
      }
    })
  },
  watch: {
    beatCounter: {
      handler(newVal) {
        if (newVal >= this.song.beats) this.beatCounter = 0
      },
    },
    'song.beats': {
      handler() {
        this.updateBeats()
      },
    },
  },
  methods: {
    ...mapMutations([
      'SCROLL_START',
      'SCROLL_STOP',
      'CLICK_START',
      'CLICK_STOP',
    ]),
    updateBeats() {
      this.beatsDOM = document.querySelectorAll('.beats-indicator__item')
    },

    loadSound() {
      this.toneVolumeNode = new Tone.Volume(this.volume).toDestination()
      this.allSounds.forEach(sound => {
        this.sounds[sound.path] = {
          high: new Tone.Player(`../media/sounds/${sound.path}/1.mp3`).connect(
            this.toneVolumeNode
          ),
          low: new Tone.Player(`../media/sounds/${sound.path}/0.mp3`).connect(
            this.toneVolumeNode
          ),
        }
      })
    },

    loadTunerSound() {
      this.allTunerSounds.forEach(sound => {
        this.tunerSounds[sound] = new Tone.Player(
          `../media/sounds/tuner/${sound}.mp3`
        ).connect(this.toneVolumeNode)
      })
    },

    setSound() {
      this.sndHigh = this.sounds[this.selectedSound].high
      this.sndLow = this.sounds[this.selectedSound].low
    },

    playStop(source) {
      if (this.synced) {
        const enabled = this.isPlaying || this.isScrolling
        enabled ? this.totalStop() : this.totalPlay()
        return
      }
      if (source === 'click') {
        this.isPlaying ? this.clickStop() : this.clickPlay()
      }
      if (source === 'scroll') {
        this.isScrolling ? this.scrollStop() : this.scrollPlay()
      }
    },

    totalPlay() {
      this.clickPlay()
      this.scrollPlay()
    },
    totalStop() {
      this.clickStop()
      this.scrollStop()
    },

    async clickPlay() {
      this.CLICK_START()

      this.createLoop()
      await Tone.start()
      this.setSound()
      Tone.Transport.start()
    },

    async clickStop() {
      this.CLICK_STOP()

      Tone.Transport.stop()
      this.beatCounter = 0
      this.setSound()
      this.loop.dispose()
      setTimeout(() => {
        this.visualClearAll()
      }, 100)
    },

    scrollPlay() {
      this.SCROLL_START()

      const container = this.$root.$refs.scrollContainer
      this.scrollCancel = this.$scrollTo(container, Number(this.scrollSpeed), {
        offset: container.scrollHeight,
        onDone: () => {
          this.SCROLL_STOP()
        },
        onCancel: () => {
          this.SCROLL_STOP()
        },
      })
    },

    scrollStop() {
      this.SCROLL_STOP()
      this.scrollCancel()
    },

    createLoop() {
      this.loop = new Tone.Loop(time => {
        this.isFirstBeat ? this.sndHigh.start(time) : this.sndLow.start(time)
        Tone.Draw.schedule(() => {
          this.visualStart(this.currentBeat)
          this.beatCounter++
        }, time)
      }, `${this.song.size}n`).start(0)
    },

    visualStart(beat) {
      this.visualClearAll()
      const styleClass = this.isFirstBeat ? 'accent-fbeat' : 'accent-beat'
      beat?.classList.add(styleClass)
    },

    visualClear(beat) {
      beat.classList.remove('accent-beat')
      beat.classList.remove('accent-fbeat')
    },

    visualClearAll() {
      this.beatsDOM.forEach(beat => {
        this.visualClear(beat)
      })
    },
  },

  computed: {
    ...mapGetters([
      'volume',
      'allSounds',
      'mute',
      'scrollSpeed',
      'isScrolling',
      'isPlaying',
      'synced',
      'tuningNote',
      'allTunerSounds',
    ]),
    previousBeat() {
      const previousElementIdx =
        this.beatCounter === 0 ? this.song.beats - 1 : this.beatCounter
      return this.beatsDOM[previousElementIdx]
    },
    currentBeat() {
      return this.beatsDOM[this.beatCounter]
    },
    isFirstBeat() {
      return this.beatCounter === 0 && this.song.sFirstBeat
    },
  },
}

export default metronomeHandler
