<template lang="pug">
.tap-button
  NeuroButton(
    :title="'Tap'",
    :returningPressed="true",
    @click-handler="tapHandler"
  )
</template>

<script>
export default {
  components: {},
  data: () => ({
    precision: 5,
    taps: [],
    ticks: [],
  }),
  watch: {},
  computed: {
    bpm() {
      let n = 0
      for (let i = this.ticks.length - 1; i >= 0; i--) {
        n += this.ticks[i]
        if (this.ticks.length - i >= this.precision) break
      }
      return Math.ceil(n / this.precision)
    },
  },
  created() {
    this.$emit('tap-handler', this.tapHandler)
  },
  methods: {
    tapHandler() {
      this.taps.push(Date.now())
      if (this.taps.length >= 2) {
        this.calcBPM()
        this.$emit('bpm', this.bpm)
      }
    },
    calcBPM() {
      for (let i = 0; i < this.taps.length; i++) {
        if (i >= 1) {
          this.ticks.push(
            Math.round(
              (60 / (this.taps[i] / 1000 - this.taps[i - 1] / 1000)) * 100
            ) / 100
          )
        }
      }
      if (this.taps.length >= 24) this.taps.shift()
    },
  },
}
</script>

