<template lang="pug">
include ../../../mixins/bemto/bemto
+b.beats-menu(:class="{ 'beats-menu--active': beatsMenuIsOpen }")
  +e.song-name(v-if="isAuth")
    +b.heading-text.--h2.--center {{ song.author }} - {{ song.title }}
  +b.heading-text.--h2.--accent.--center {{ song.bpm }} bpm
  +b.heading-text.--h2.--accent.--center {{ song.beats }} / {{ song.size }}
  Input(
    v-for="(input, idx) in inputs",
    :key="idx",
    :propsToMerge="input",
    @input-handler="inputHandler"
  )
  ButtonType1(@click-handler="clickHandler", :title="isUpdated&&isAuth?'save changes':'close'")
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import inputs from './inputsFields.js'
export default {
  mixins: [inputs],
  methods: {
    ...mapMutations([
      'UPDATE_SONG',
      'CLOSE_ALL_MENU',
      'CHANGE_CURRENT_SFIRSTBEAT',
      'CHANGE_CURRENT_SIZE',
      'CHANGE_CURRENT_BEATS',
      'CHANGE_CURRENT_BPM',
    ]),
    inputHandler(val, model) {
      this[`CHANGE_CURRENT_${model.toUpperCase()}`](val)
    },
    clickHandler() {
      if (this.isUpdated && this.isAuth) this.UPDATE_SONG(this.song)
      this.CLOSE_ALL_MENU()
    },
  },
  computed: {
    ...mapGetters([
      'beatsMenuIsOpen',
      'song',
      'constants',
      'isUpdated',
      'isAuth',
    ]),
  },
}
</script>
