<template lang="pug">
include ../../mixins/bemto/bemto
+b.sign-in-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.go(-1)")
  +e.form.FORM
    fieldset
      +b.heading-text.--h2.--right.LEGEND sign-in
      Input(
        v-for="(input, idx) in inputs",
        :key="idx",
        :propsToMerge="input",
        @input-handler="inputHandler"
      )
      +e.buttons
        ButtonType1(:title="'log-in'", @click-handler="checkForm")
        +e.actions
          +e.icon(@click="signInWithSocial('Google')")
            Icon(
              :iconClass="'icon-google'",
              :small="true",
              :isHovered="false",
            )
          +e.icon(@click="signInWithSocial('Facebook')")
            Icon(
              :iconClass="'icon-facebook'",
              :small="true",
              :isHovered="false",
              )
          +b.link.--l3.--center(@click="$router.push('/resetPassword')") forgot password?
          +b.link.--l3.--center(@click="$router.push('/signUp')") sign-up now
</template>

<script>
import { required, email, minLength } from 'vuelidate/lib/validators'
import { mapMutations, mapActions } from 'vuex'
import validateErrorHandler from '@/mixins/validateErrorHandler'
import inputsFields from '@/views/SignIn/inputsFields.js'
export default {
  mixins: [inputsFields, validateErrorHandler],
  data: () => ({
    message: '',
    form: {
      email: '',
      password: '',
    },
  }),
  validations: {
    form: {
      email: {
        required,
        email,
      },
      password: {
        required,
        minLength: minLength(6),
      },
    },
  },
  methods: {
    ...mapActions(['signIn', 'signInWithSocial', 'logOut']),
    ...mapMutations(['SET_NEW_STATE', 'RESET_STORE']),
    inputHandler(val, model) {
      this.form[model] = val.trim()
      this.$v.form[model].$touch()
    },
    async checkForm() {
      if (this.$v.form.$invalid) {
        this.$v.form.$touch()
        return
      }
      this.signIn({ ...this.form })
    },
  },
}
</script>
