const inputsFields = {
  computed: {
    inputs() {
      return [
        {
          Input: {
            componentName: 'Input',
            label: 'Author',
            fieldName: 'author',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler(
              'author',
              ['required', 'minLength'],
              'song'
            ),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'author',
            value: this.song.author,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Title',
            fieldName: 'title',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler(
              'title',
              ['required', 'minLength'],
              'song'
            ),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'title',
            value: this.song.title,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Bpm',
            fieldName: 'bpm',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'bpm',
            componentName: 'NeuroInputSlider',
            min: this.constants.minBpm,
            max: this.constants.maxBpm,
            value: this.song.bpm,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Beats',
            fieldName: 'beats',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'beats',
            componentName: 'NeuroInputSlider',
            value: this.song.beats,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Size',
            fieldName: 'size',
            childComponentName: 'NeuroInputSlider',
          },
          NeuroInputSlider: {
            modelIdent: 'size',
            componentName: 'NeuroInputSlider',
            min: 2,
            max: 16,
            step: 2,
            value: this.song.size,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Stress first beat',
            fieldName: 'beatsMenu-stressFirstBeat',
            childComponentName: 'NeuroInputSwitch',
            horAlign: 'd-flex justify-content-end',
          },
          NeuroInputSwitch: {
            modelIdent: 'sFirstBeat',
            componentName: 'NeuroInputSwitch',
            isActive: this.song.sFirstBeat,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Song text',
            fieldName: 'text',
            childComponentName: 'NeuroInputText',
          },
          NeuroInputText: {
            modelIdent: 'text',
            componentName: 'NeuroInputText',
            value: this.song.text,
            inputType: 'textarea',
          },
        },
      ]
    },
  },
}
export default inputsFields
