<template lang="pug">
include ../../../mixins/bemto/bemto
+b.down-bar
  +e.play-stop
    NeuroButton(
      :title="playBtnTitle",
      :isActive="isPlaying",
      @click-handler="playStop('click')"
    )
  +e.sounds
    ButtonType1(
      :title="allSounds[soundIndex].name",
      @click-handler="changeSound"
    )
  TapButton(@bpm="CHANGE_CURRENT_BPM", @tap-handler="$event=>tap=$event")

</template>
<script>
import playStopHandler from '@/mixins/playStopHandler'
import TapButton from '@/components/composite/TapButton/TapButton'
import { mapGetters, mapMutations } from 'vuex'
export default {
  mixins: [playStopHandler],
  components: { TapButton },
  data: () => ({
    soundIndex: 0,
  }),
  created() {
    this.setSoundIndex()
    window.addEventListener('keypress', this.onKeyPress)
  },
  beforeDestroy() {
    window.removeEventListener('keypress', this.onKeyPress)
  },
  methods: {
    onKeyPress(e) {
      switch (e.code) {
        case 'Space':
          this.playStop('click')
          break
        case 'Enter':
          this.tap()
          break
      }
    },
    ...mapMutations(['CHANGE_CURRENT_BPM', 'CHANGE_SELECTED_SOUND']),
    setSoundIndex() {
      this.allSounds.forEach((obj, i) => {
        if (obj.path === this.selectedSound) this.soundIndex = i
      })
    },
    changeSound() {
      if (this.soundIndex >= this.allSounds.length - 1) {
        this.soundIndex = -1
      }
      this.soundIndex++
      this.CHANGE_SELECTED_SOUND(this.allSounds[this.soundIndex].path)
    },
  },
  computed: {
    ...mapGetters(['song', 'selectedSound', 'allSounds', 'testSettings']),
    playBtnTitle() {
      return this.isPlaying ? 'stop' : 'play'
    },
  },
}
</script>
