const inputsFields = {
  computed: {
    inputs() {
      return [
        {
          Input: {
            componentName: 'Input',
            label: 'Email',
            fieldName: 'email',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler('email', ['required', 'email']),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'email',
            value: this.form.email,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Password',
            fieldName: 'password',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler('password', ['required', 'minLength']),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'password',
            value: this.form.password,
            type: 'password',
          },
        },
      ]
    },
  },
}
export default inputsFields
