<template lang="pug">
include ../../../mixins/bemto/bemto
+b.song-line(@click="LOAD_SONG(song.id)")
  +e.top-side
    +e.status(:class="statusClass(song.id)")
    +e.title
      +b.text.--p3  {{ song.author }} - {{ song.title }}
      +e.hasText(v-if="song.text") {{`[T]`}}
  +e.bottom-side
    +b.text.--p3.song-line__bpm {{ song.bpm }} bpm
    +b.text.--p3.nowrap.song-line__beats {{ song.beats }} / {{ song.size }}
    +e.buttons
      .btn(@click.stop="$router.push(`editSong/${song.id}`)")
        ButtonType1(:title="'edit'")
      .btn(v-if="showDeleteBtn" @click.stop="DELETE_SONG(song.id)")
        ButtonType1(:title="'delete'" :color="'red'")
</template>
<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  props: {
    showDeleteBtn: {
      type: Boolean,
      default() {
        return true
      },
    },
    song: {
      type: Object,
      default() {
        return {
          id: '00000000',
          author: 'new author',
          title: 'new title',
          text: 'new text',
          bpm: 120,
          beats: 4,
          size: 4,
        }
      },
    },
  },
  computed: {
    ...mapGetters(['isSongActive']),
  },
  methods: {
    ...mapMutations(['LOAD_SONG', 'DELETE_SONG']),
    statusClass(id) {
      if (this.isSongActive(id)) return 'song-line__status--active'
    },
  },
}
</script>
