const validateErrorHandler = {
  methods: {
    errorHandler(element, errors, key = 'form') {
      let message
      errors.forEach(error => {
        const checkProperty = this.$v[key][element][error]
        const dirty = this.$v[key][element].$dirty
        if (!checkProperty && dirty) {
          switch (error) {
            case 'required':
              message = 'field is required'
              break
            case 'email':
              message = 'incorrect email'
              break
            case 'minLength':
              // eslint-disable-next-line no-case-declarations
              const minLength = this.$v[key][element].$params.minLength.min
              message = `min length is ${minLength}`
              break
          }
        }
      })
      return message
    },
  },
}
export default validateErrorHandler
