<template lang="pug">
include ../../../mixins/bemto/bemto
+b.top-bar
  +e.beats-btn
    NeuroButton(
      :title="beatsMenuTitle",
      :isActive="beatsMenuIsOpen",
      @click-handler="TOGGLE_MENU({valueName:'beatsMenuIsOpen'})"
    )
  +e.menu-btn
    NeuroButton(
      :title="'menu'",
      :isActive="menuIsOpen",
      @click-handler="TOGGLE_MENU({valueName:'menuIsOpen'})"
    )
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  components: {},
  methods: {
    ...mapMutations(['TOGGLE_MENU']),
  },
  computed: {
    ...mapGetters(['menuIsOpen', 'beatsMenuIsOpen', 'song']),
    beatsMenuTitle() {
      return `${this.song.beats} / ${this.song.size}`
    },
  },
}
</script>
