<template lang="pug">
include ../../../mixins/bemto/bemto
+b.volume
  +e.info
    +b.text.--p4 VOLUME
    Icon(
      :iconClass="isMute ? 'icon-volume-mute' : 'icon-volume'",
      :small="true",
      :isHovered="false",
      @click-handler="muteBtnHandler"
    )
  +e.container
    label(for="volSlider")
      input#volSlider(
        type="range",
        v-model.number="vol",
        :min="minVolumeDb",
        :max="maxVolumeDb",
        step='0.001',
        @change="CHANGE_VOL(vol)"
      )

</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data: () => ({
    vol: 0,
    lastVolumeValue: 0,
  }),
  created() {
    this.vol = this.volume
  },
  watch: {
    vol(val) {
      // emit event to make slider smoothly
      this.$root.$emit('current-volume', val, this.isMute)
    },
  },
  methods: {
    ...mapMutations(['CHANGE_VOL']),
    muteBtnHandler() {
      if (this.isMute) this.vol = this.lastVolumeValue
      else {
        this.lastVolumeValue = this.vol
        this.vol = this.minVolumeDb
      }
    },
  },
  computed: {
    ...mapGetters(['volume', 'minVolumeDb', 'maxVolumeDb']),
    isMute() {
      return this.vol === this.minVolumeDb
    },
  },
}
</script>
