const helpers = {
  clg: function(message, color, clear) {
    const colors = {
      success: 'background: black; color: #bada55',
      error: 'background: black; color: red',
      info: 'background: black; color: blue',
    }
    if (process.env.NODE_ENV === 'development') {
      if (clear) console.clear()
      console.log(`%c ${message} `, colors[color] ?? colors.info)
    }
  },
}

export default helpers
