import Vue from 'vue'
import firebase from 'firebase/app'
import router from '../router'

const defaultState = () => {
  return {
    uid: '',
    name: '',
    email: '',
    alternativeAvatar: '',
    isAuth: false,
  }
}
const auth = {
  state: defaultState(),
  getters: {
    userData: state => state,
    uid: state => state.uid,
    name: state => state.name,
    email: state => state.email,
    alternativeAvatar: state => state.alternativeAvatar,
    isAuth: state => state.isAuth,
  },
  mutations: {
    SET_USER_DATA(state, data) {
      state.uid = data.uid
      state.name = data.displayName
      state.email = data.email
      state.alternativeAvatar = data.photoURL
    },
    RESET_AUTH_STATE(state) {
      Object.assign(state, defaultState())
    },
    SET_AUTH(state, auth) {
      state.isAuth = auth
    },
  },
  actions: {
    async initWatchAuth({ dispatch, getters }) {
      firebase.auth().onAuthStateChanged(async user => {
        if (user) {
          Vue.prototype.$clg('Authorized', 'success')
          let debounceTimer = {}
          this.subscribe(mutation => {
            const matchMutation = getters.mutationsInitSave.some(
              elem => elem === mutation.type
            )
            if (matchMutation) {
              clearTimeout(debounceTimer)
              debounceTimer = setTimeout(async () => {
                await dispatch('saveDataToFB')
              }, 2000)
            }
          })
          this.commit('SET_AUTH', true)
        } else {
          Vue.prototype.$clg('Unauthorized', 'error')
          this.commit('RESET_STORE')
          this.commit('SET_AUTH', false)
        }
      })
    },

    async allowAccess({ dispatch, commit }) {
      const user = firebase.auth().currentUser
      try {
        if (user.emailVerified) {
          commit('SET_USER_DATA', user)
          await dispatch('loadDataToVuexFromFB')
          router.push('./').catch(() => {})
          this.commit('TOGGLE_MENU', { valueName: 'menuIsOpen', delay: 1000 })
          commit('SHOW_SNACKBAR', {
            type: 'success',
            text: `Hello, ${user.displayName}`,
          })
        } else throw new Error('Please confirm your email')
      } catch (e) {
        await firebase.auth().signOut()
        console.error(e)
        commit('SHOW_SNACKBAR', { type: 'error', text: e.message })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async signInWithSocial({ commit, dispatch }, providerName) {
      commit('TURN_ON_LOADER')
      try {
        const provider = new firebase.auth[`${providerName}AuthProvider`]()
        await firebase.auth().signInWithPopup(provider)
        await dispatch('allowAccess')
      } catch (e) {
        console.error(e)
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: e.message,
          timeout: 7000,
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async signIn({ commit, dispatch }, credential) {
      commit('TURN_ON_LOADER')
      try {
        await firebase
          .auth()
          .signInWithEmailAndPassword(credential.email, credential.password)
        await dispatch('allowAccess')
      } catch (e) {
        console.error(e.message)
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: e.message,
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async createUser({ commit }, credential) {
      commit('TURN_ON_LOADER')
      try {
        const result = await firebase
          .auth()
          .createUserWithEmailAndPassword(credential.email, credential.password)
        await result.user.updateProfile({
          displayName: credential.name,
        })
        await firebase.auth().currentUser.sendEmailVerification()
        router.push('./SignIn').catch(() => {})
        commit('SHOW_SNACKBAR', {
          type: 'success',
          text: 'account created, check your inbox to confirm email',
          timeout: 5000,
        })
      } catch (e) {
        console.error(e)
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: e.message,
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async loadDataToVuexFromFB({ dispatch, commit, getters }) {
      try {
        firebase
          .database()
          .ref(`/users/${getters.uid}/`)
          .on('value', async snap => {
            const data = snap.val()?.data
            if (data) await dispatch('updateStore', data)
            else await dispatch('saveDataToFB')
          })
      } catch (e) {
        console.error(e)
        router.push('./SignIn').catch(() => {})
        commit('SHOW_SNACKBAR', { type: 'error', text: e.message })
      }
    },

    async saveDataToFB({ commit, getters }) {
      try {
        const data = getters.dataToSave
        await firebase
          .database()
          .ref(`/users/${getters.uid}/`)
          .set({ data })
        Vue.prototype.$clg('Data is saved to firebase', 'success')
      } catch (e) {
        console.error(e)
        commit('SHOW_SNACKBAR', { type: 'error', text: e.message })
      }
    },

    async updateProfileData({dispatch, commit }, credential) {
      commit('TURN_ON_LOADER')
      try {
        const user = firebase.auth().currentUser
        await user.updateProfile({
          displayName: credential.name ?? user.displayName,
          photoURL: credential.photoURL ?? user.photoURL,
        })
        if (credential.email !== user.email)
          await user.updateEmail(credential.email)
        commit('SET_USER_DATA', user)
        commit('SHOW_SNACKBAR', {
          type: 'success',
          text: `credential updated, if you change email don't forget to verify`,
        })
        setTimeout(async ()=>{
          await user.sendEmailVerification()
          router.push('./SignIn').catch(() => {})
          await dispatch('logOut')
        },2000)
      } catch (e) {
        console.error(e)
        router.push('./SignIn').catch(() => {})
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: 'you are not authorized',
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async resetPassword({ commit }, email) {
      commit('TURN_ON_LOADER')
      try {
        await firebase.auth().sendPasswordResetEmail(email)
        commit('SHOW_SNACKBAR', {
          type: 'success',
          text: 'Password updated, checkout email',
        })
        router.push('./SignIn').catch(() => {})
      } catch (e) {
        console.error(e)
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: e.message,
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },

    async logOut({ commit }) {
      commit('TURN_ON_LOADER')
      try {
        await firebase.auth().signOut()
        router.push('./SignIn').catch(() => {})
        commit('CLOSE_ALL_MENU')
        commit('SHOW_SNACKBAR', {
          type: 'info',
          text: 'logged out',
        })
      } catch (e) {
        console.error('logout error', e)
        commit('SHOW_SNACKBAR', {
          type: 'error',
          text: e.message,
        })
      } finally {
        commit('TURN_OFF_LOADER')
      }
    },
  },
}
export default auth
