<template lang="pug">
include ../../../mixins/bemto/bemto
+b.main-slider
  VueSlickCarousel(v-bind="{...sliderConfig,slidesToShow}")
    .slide
      SongText
    .slide(v-if="$screen.width>1200")
      +e.middle
        BpmIndicator
        BpmKnob
    .slide
      SongList
    .slide(v-if="$screen.width<=1200")
      +e.middle
        BpmIndicator
        BpmKnob
</template>

<script>
import SongList from '@/components/composite/SongList/SongList'
import SongText from '@/components/composite/SongText/SongText'

export default {
  components: { SongList, SongText },
  data: () => ({
    slidesToShow: 3,
  }),
  watch: {
    '$screen.width': {
      immediate: true,
      handler(val) {
        if (val > 1200) this.slidesToShow = 3
        if (val <= 1200) this.slidesToShow = 2
        if (val <= 768) this.slidesToShow = 1
      },
    },
  },
  computed: {
    sliderConfig() {
      return {
        dots: false,
        arrows: false,
        centerMode: false,
        infinite: true,
        swipe: true,
        initialSlide: 0,
      }
    },
  },
}
</script>
