<template lang="pug">
include ../../../mixins/bemto/bemto
+b.info-line
  +e.container(v-if="isAuth")
    +e.save-update
      ButtonType1(:title="isUpdated ? 'update song' : 'edit song'", @click-handler="clickHandler")
    +e.song-title(@click="$router.push('/Songs')")
      VueSlickCarousel(
        infinite=true,
        autoplay=true,
        :arrows='false',
        easing='linear',
        cssEase='linear',
        waitForAnimate=false,
        :speed='20000',
        centerMode=true,
        :edgeFriction='0',
        pauseOnFocus=false,
        pauseOnHover=false,
        pauseOnDotsHover=false,
        autoSlidesToShow=true,
        variableWidth=true,
        :adaptiveHeight='true'
      )
        .song-author {{ song.author }}
        .divider -
        .song-title {{ song.title }}
        .divider
  +e.logo(v-else)
    +e.logo-wrapper(@click="$router.push('/SignIn')")
      Logo(:lineWidth="'50'")
      +e.app-name {{appName.toUpperCase()}}
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'

export default {
  methods: {
    ...mapMutations(['UPDATE_SONG', 'SET_INFO_MESSAGE']),
    clickHandler() {
      this.isUpdated
        ? this.UPDATE_SONG(this.song)
        : this.$router.push(`editSong/${this.song.id}`)
    },
  },
  computed: {
    ...mapGetters(['song', 'isAuth', 'isUpdated', 'appName']),
    runningLineConfig() {
      return {
        autoplay: 'true',
        infinite: 'true',
        autoplaySpeed: '0',
        arrows: 'false',
        easing: '"linear"',
        cssEase: '"linear"',
        waitForAnimate: 'false',
        speed: '20000',
        centerMode: 'true',
        edgeFriction: '0',
        pauseOnFocus: 'false',
        pauseOnHover: 'false',
        pauseOnDotsHover: 'false',
        autoSlidesToShow: 'true',
        variableWidth: 'true',
      }
    },
  },
}
</script>
