import Vue from 'vue'
import Vuex from 'vuex'
import constants from './constants'
import auth from './auth'
import system from './system'
import settings from './settings'
import songsData from './songsData'
import SecureLS from 'secure-ls'
import _pick from 'lodash/pick'

var ls = new SecureLS({ isCompression: false })

Vue.use(Vuex)

import createPersistedState from 'vuex-persistedstate'

const dataState = createPersistedState({
  paths: ['auth', 'songsData', 'settings'],
  storage: {
    getItem: key => ls.get(key),
    setItem: (key, value) => ls.set(key, value),
    removeItem: key => ls.remove(key),
  },
})

export default new Vuex.Store({
  modules: {
    constants,
    auth,
    system,
    settings,
    songsData,
  },
  plugins: [dataState],
  state: () => ({
    modulesNames: ['constants', 'auth', 'system', 'settings', 'songsData'],
    mutationsInitSave: [
      'CHANGE_THEME',
      'CHANGE_VOL',
      'CHANGE_SELECTED_SOUND',
      'ADD_SONG',
      'UPDATE_SONG',
      'DELETE_SONG',
      'SET_AVATAR',
      'CHANGE_FZ_MOD',
      'CHANGE_SCROLL_SPEED',
      'TOGGLE_SYNCED',
    ],
    modulesToSave: ['settings', 'songsData'],
  }),
  actions: {
    updateStore({ commit }, data) {
      Object.keys(data).forEach(module => {
        const moduleName = module
          .split(/(?=[A-Z])/)
          .join('_')
          .toUpperCase()
        commit(`SET_${moduleName}`, data[module])
      })
      Vue.prototype.$clg(`Store updated`, 'success')
    },
  },
  mutations: {
    RESET_STORE(state) {
      state.modulesNames.forEach(module => {
        const mutationName = `RESET_${module.toUpperCase()}_STATE`
        // check mutation exist
        const possibleMutations = Object.keys(this._mutations)
        if (possibleMutations.some(mutation => mutation === mutationName))
          this.commit(mutationName)
      })
      localStorage.clear()
      Vue.prototype.$clg('Store reset', 'success')
    },
  },
  getters: {
    dataToSave: state => _pick(state, state.modulesToSave),
    mutationsInitSave: state => state.mutationsInitSave,
  },
})
