const inputsFields = {
  computed: {
    inputs() {
      return [
        {
          Input: {
            componentName: 'Input',
            label: 'Name',
            fieldName: 'name',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler('name', ['required', 'minLength']),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'name',
            value: this.form.name,
          },
        },
        {
          Input: {
            componentName: 'Input',
            label: 'Email',
            fieldName: 'email',
            childComponentName: 'NeuroInputText',
            messageType: 'error',
            message: this.errorHandler('email', ['required', 'email']),
          },
          NeuroInputText: {
            componentName: 'NeuroInputText',
            modelIdent: 'email',
            value: this.form.email,
          },
        },
      ]
    },
  },
}
export default inputsFields
