<template lang="pug">
include ../../mixins/bemto/bemto
+b.songs-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.go(-1)")
  +b.heading-text.--h2.--right Songs
  InputSelect(:label="'sort by'" :options="options" @input="sortSongs")
  +e.songs
    SongLine(
      v-for="song in songs",
      :key="song.id",
      :song="song"
    )
  +e.add-btn
    ButtonType1(:title="'ADD SONG +'" @click-handler="$router.push('/AddSong')")
</template>
<script>
import { mapGetters, mapMutations } from 'vuex'
import SongLine from '@/components/composite/SongLine/SongLine'

export default {
  components: { SongLine },
  data: () => ({
    options: [
      { id: 'author reversed', title: 'Author(A-Z)' },
      { id: 'author', title: 'Author(Z-A)' },
      { id: 'title reversed', title: 'Title(A-Z)' },
      { id: 'title', title: 'Title(Z-A)' },
      { id: 'bpm', title: 'Bpm(up-to-down)' },
      { id: 'bpm reversed', title: 'Bpm(down-to-up)' },
      { id: 'timestamp', title: 'Date(early first)' },
      {
        id: 'timestamp reversed',
        title: 'Date(late first)',
        selected: true,
      },
    ],
  }),
  computed: {
    ...mapGetters(['songs']),
  },
  methods: {
    ...mapMutations(['LOAD_SONG', 'DELETE_SONG']),
    sortSongs(type) {
      const isReversed = type.split(' ').length > 1
      this.songs.sort(
        (current, next) => `${current[type] > next[type] ? -1 : 1}`
      )
      if (isReversed) this.songs.reverse()
    },
  },
}
</script>
