<template lang="pug">
include ./mixins/bemto/bemto
+b.app
  NotSupport
  Loader(v-show="isLoading")
  Snackbar
  BeatsMenu
  Menu
  +e.body(:class="menuClasses", @click="closeMenuHandler($event)")
    +e.container
      +e.page.ROUTER-VIEW
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import BeatsMenu from '@/components/layout/BeatsMenu/BeatsMenu'
import Menu from '@/components/layout/Menu/Menu'
import Snackbar from '@/components/composite/Snackbar/Snackbar'
import NotSupport from '@/views/NotSupport/NotSupport'

export default {
  name: 'App',
  components: {
    BeatsMenu,
    Menu,
    Snackbar,
    NotSupport,
  },
  data: () => ({
    menus: ['menuIsOpen', 'beatsMenuIsOpen'],
  }),
  created() {
    this.initWatchAuth()
  },
  methods: {
    ...mapActions(['initWatchAuth']),
    ...mapMutations(['CLOSE_ALL_MENU', 'UPDATE_THEME']),
    closeMenuHandler(e) {
      if (e.target.classList.contains('covered')) this.CLOSE_ALL_MENU()
    },
  },
  computed: {
    ...mapGetters(['menuIsOpen', 'beatsMenuIsOpen', 'isLoading', 'isAuth']),
    menuClasses() {
      let classes = ''
      if (this.menuIsOpen) classes = 'app--menu-active covered'
      if (this.beatsMenuIsOpen) classes = 'app--beats-menu-active covered'
      return classes
    },
  },
}
</script>
