<template lang="pug">
include ../../mixins/bemto/bemto
+b.sign-up-page
  Icon(:iconClass="'icon-arrowLeft'", :small="true" @click-handler="$router.push('/')")
  +e.form.FORM
    fieldset
      +b.heading-text.--h2.--right.LEGEND sign-up
      Input(
        v-for="(input, idx) in inputs",
        :key="idx",
        :propsToMerge="input",
        @input-handler="inputHandler"
      )
      +e.policy
        Checkbox(
          :text="'I have read and agree'",
          :value="policy",
          @change-handler="policyHandler"
        )
        +b.link.--l5.--accent(@click="$router.push('/policy')") privacy policy
      +e.buttons
        ButtonType1(
          :title="'sign up'",
          :disabled="$v.form.$invalid",
          @click-handler="registerUser"
        )
      +e.sign-in-btn
        +b.link.--l3(@click="$router.push('/signIn')") sign-in
</template>

<script>
import { required, email, minLength } from 'vuelidate/lib/validators'
import { mapActions, mapMutations } from 'vuex'
import inputsFields from '@/views/SignUp/inputsFields.js'
import validateErrorHandler from '@/mixins/validateErrorHandler'

export default {
  mixins: [inputsFields, validateErrorHandler],
  data: () => ({
    policy: true,
    form: {
      name: '',
      email: '',
      password: '',
    },
  }),
  validations: {
    form: {
      name: {
        required,
        minLength: minLength(1),
      },
      email: {
        required,
        email,
      },
      password: {
        required,
        minLength: minLength(6),
      },
    },
  },
  methods: {
    ...mapActions(['createUser']),
    ...mapMutations(['SET_USER_NAME', 'SET_INFO_MESSAGE', 'RESET_STORE']),
    policyHandler(val) {
      this.policy = val
    },
    inputHandler(val, model) {
      this.form[model] = val.trim()
      this.$v.form[model].$touch()
    },
    registerUser() {
      if (this.$v.form.$invalid) {
        this.$v.form.$touch()
        return
      }
      this.createUser({ ...this.form })
    },
  },
}
</script>
